	import cuCustom from '@/colorui/components/cu-custom.vue'
	import asyncSwitch from "@/components/helang-asyncSwitch/helang-asyncSwitch.vue"
	import mpopup from '../../components/xuan-popup/xuan-popup.vue'
export default {
	data() {
		return {
			orderId : null,
			orderTotal:0,
			usersTotal:0,
			// 付款选择
			valSelect:'',
			previousPage:''
		}
	},
	onLaunch(e) {
		// this.orderId = e.id
		// this.previousPage = e
	},
	onLoad(e) {
		// let data = e
		this.getdataa()
	},
	onShow(e){
		// let data = e
		this.getdataa()
		this.orderMoney()
	},
	methods: {
		radio(value) {
			// console.log(value)
			this.valSelect = value.detail.value
		},
		async getdataa(){
			let data = uni.getStorageSync('pay')
			data = JSON.parse(data)
			this.orderId = data.id
			this.previousPage = data
		},
		orderMoney() {
			let data = uni.getStorageSync('pay')
			data = JSON.parse(data)
			this.orderId = data.id
			this.previousPage = data
			// console.log(this.orderId)
			uni.request({
				// 接口
				url: 'http://47.104.29.236:9999/api/v1/getOrderMoney',
				// 请求方式
				method: 'GET',
				// 携带的数据
				data: {id:this.orderId},
				// 请求成功
				success: res => {
					console.log(res)
					this.orderTotal = res.data.data[0].price
				},
				// 请求失败
				fail: () => {},
				complete: () => {}
			})
		},
		usersMoney() {
			let id = uni.getStorageSync('user_id')
			uni.request({
				// 接口
				url: 'http://47.104.29.236:9999/api/v1/getUsersMoney',
				// 请求方式
				method: 'GET',
				// 携带的数据
				data: {id:id},
				// 请求成功
				success: res => {
					// console.log(res)
					// this.orderTotal = res.data.data[0].price
					this.usersTotal = res.data.data[0].price
				},
				// 请求失败
				fail: () => {},
				complete: () => {}
			})
		},
		paymentBtn() {
			let id = uni.getStorageSync('user_id')
			if(this.valSelect == 1 || this.valSelect == 2) {
				uni.navigateTo({
					url:'./paymentWin',
					success:res =>{},
					fail:()=>{},
					complete:()=>{}
				})
				uni.request({
					// 接口
					url: 'http://47.104.29.236:9999/api/v1/updateOrderMoneyAll',
					// 请求方式
					method: 'POST',
					// 携带的数据
					data: {
						price:this.usersTotal - this.orderTotal,
						id:id,
						order_id:this.orderId,
						valSelect:this.valSelect,
						 },
					// 请求成功
					success: res => {
						console.log(res)
						// this.orderTotal = res.data.data[0].price
					},
					// 请求失败
					fail: () => {},
					complete: () => {}
				})
			} else if(this.valSelect == 3) {
				if(this.usersTotal > this.orderTotal) {
					uni.navigateTo({
						url:'./paymentWin',
						success:res =>{},
						fail:()=>{},
						complete:()=>{}
					})
					let id = uni.getStorageSync('user_id')
					uni.request({
						// 接口
						url: 'http://47.104.29.236:9999/api/v1/updateOrderMoney',
						// 请求方式
						method: 'POST',
						// 携带的数据
						data: {price:this.usersTotal - this.orderTotal,id:id},
						// 请求成功
						success: res => {
							console.log(res)
							this.orderTotal = res.data.data[0].price
						},
						// 请求失败
						fail: () => {},
						complete: () => {}
					})
					uni.request({
						// 接口
						url: 'http://47.104.29.236:9999/api/v1/updateOrderMoneyAll',
						// 请求方式
						method: 'POST',
						// 携带的数据
						data: {price:this.usersTotal - this.orderTotal,id:id, valSelect:this.valSelect,	order_id:this.orderId},
						// 请求成功
						success: res => {
							console.log(res)
							this.orderTotal = res.data.data[0].price
						},
						// 请求失败
						fail: () => {},
						complete: () => {}
					})
				} else {
					this.$refs.mpopup.open({
					    type:'err',
					    content:'您的账户余额不足',
					    timeout:1000,
					    isClick:false
					});
				}
			} else {
				this.$refs.mpopup.open({
				    type:'err',
				    content:'请选择支付方式',
				    timeout:1000,
				    isClick:false
				});
			}
		}
	},
	created() {
		this.orderMoney()
		this.usersMoney()
	},
	onUnload(e) {
		this.orderMoney()
		// this.orderId = e.id
	},
	components: {
		cuCustom,
		"async-switch":asyncSwitch,
		mpopup
	},
}
